.advantages {
  @include container();
  padding-top: 120px;
  padding-bottom: 120px;
  background-image: image-set(url("../img/advantages-bg-desktop.png") 1x,
      url("../img/advantages-bg-desktopx2.png") 2x);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #b0c4de;
  color: $color-dark-blue;

  @include vp-1023 {
    padding-top: 100px;
    padding-bottom: 100px;
    background-image: image-set(url("../img/advantages-bg-tablet.png") 1x,
        url("../img/advantages-bg-tabletx2.png") 2x);
  }

  @include vp-767 {
    padding-top: 65px;
    padding-bottom: 65px;
    background-image: image-set(url("../img/advantages-bg-mobile.png") 1x,
        url("../img/advantages-bg-mobilex2.png") 2x);
  }
}

.advantages__title {
  text-align: center;
  margin: 0;
  margin-top: -10px;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  margin-bottom: 49px;

  @include vp-1023 {
    font-size: 26px;
    line-height: 39px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 36px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;

  @include vp-1023 {
    gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  background-color: $color-white;
  padding: 30px;
  max-width: 360px;
  min-height: 241px;
  width: 100%;

  @include vp-1023 {
    padding: 25px 20px 20px;
  }

  @include vp-767 {
    min-height: 250px;
    margin: 0 auto;
  }
}

.advantages__item:first-child {
  @include vp-1023 {
    padding-right: 16px;
  }
}
